<template>
    <div class="tips warning" v-if="warning" transition="fade">
        <p>
            {{ text }}
        </p>
    </div>
    <div class="tips notifa" v-if="notifa" transition="fade">
        <p>
            {{ text }}
        </p>
    </div>
    <div class="tips success" v-if="success" transition="fade">
        <p>
            {{ text }}
        </p>
    </div>
</template>

<script>
export default {
    props: {
        warning: {
            type: Boolean,
            default: false
        },
        notifa: {
            type: Boolean,
            default: false
        },
        success: {
            type: Boolean,
            default: false
        },
        text: {
            type: String,
            default: 'Warning'
        }
    }
}
</script>

<style lang="less" scoped>
.tips {
    width: 100%;
    padding: 20px 40px 0 40px;

    p {
        padding: 10px;
        color: #FFF;
        font-size: 14px;
        border-radius: 5px;
        display: block;

        span {
            float: right;
            cursor: pointer;
        }
    }
}
.warning {
    p {
        border: 1px solid #F45757;
        background: rgba(255, 0, 0, .5);
    }
}
.notifa {
    p {
        color: #b8a41a !important;
        border: 1px solid #ffde00;
        background: rgba(255, 239, 135, .5);
    }
}
.success {
    p {
        border: 1px solid #22c20d;
        background: rgba(34, 194, 13, .5);
    }
}

/* 必需 */
.fade-transition {
    transition: all .5s ease;
    opacity: 1;
}

/* .fade-enter 定义进入的开始状态 */
/* .fade-leave 定义离开的结束状态 */
.fade-enter, .fade-leave {
    opacity: 0;
}
</style>


