<template>
    <div class="page">
        <notification :warning="notifa.warning" :success="notifa.success" :text="notifa.text"></notification>
        <div class="item">
            <div class="left">用户名</div>
            <div class="right">
                <input type="text" v-model="newstd.username">
                <span class="inputTips">登录和显示的用户名，推荐使用学号</span>
            </div>
        </div>
        <div class="item">
            <div class="left">初始密码</div>
            <div class="right">
                <input type="password" v-model="newstd.password">
                <span class="inputTips">默认为123456，可以手动指定。</span>
            </div>
        </div>
        <div class="item">
            <div class="left">账户等级</div>
            <div class="right">
                <input type="text" v-model="newstd.rank">
                <span class="inputTips">默认为1，数字越大权限越多，最多为5。填写超过5则默认为5。</span>
            </div>
        </div>
        <div class="item">
            <div class="replyBtn">
                <button :class="{ able: btnAble }" @click="addstudent">添加学生</button>
            </div>
        </div>
    </div>    
</template>

<script>
import notification from '../../../components/notification.vue'

export default {
    data () {
        return {
            newstd: {
                username: '',
                password: '123456',
                rank: '1'
            },
            btnAble: false,
            showwaring: false,
            notifa: {
                warning: false,
                success: false,
                text: ''
            }
        }
    },
    computed: {
        'btnAble': function () {
            return (this.newstd.username !== '')
        }
    },
    components: {
        notification
    },
    methods: {
        addstudent () {
            if (parseInt(this.newstd.rank) > 5) {
                this.newstd.rank = '5'
            }
            if (!this.btnAble) return
            this.$http.post('http://szdx.sinaapp.com/dashboard/rest/v1/setting/addstudent.php', this.newstd)
                .then(response => {
                    if (response.data === 'success') {
                        this.notifa.text = '添加用户成功！'
                        this.notifa.success = true
                        setTimeout(() => {
                            this.notifa.success = false
                        }, 2000)
                    } else if (response.data === 'failed') {
                        this.notifa.text = '添加用户失败。'
                        this.notifa.warning = true
                        setTimeout(() => {
                            this.notifa.warning = false
                        }, 2000)
                    } else {
                        this.notifa.text = '服务器故障！'
                        this.notifa.warning = true
                        setTimeout(() => {
                            this.notifa.warning = false
                        }, 2000)
                    }
                }, () => {
                    this.notifa.text = '服务器故障！'
                    this.notifa.warning = true
                    setTimeout(() => {
                        this.notifa.warning = false
                    }, 2000)
                })
        }
    }
}
</script>

<style lang="less" scoped>
.page {
    float: left;
    width: 50%;
    padding: 20px 40px 40px 10px;

    .item {
        float: left;
        width: 100%;
        margin-top: 5px;

        .left {
            float: left;
            width: 20%;
            padding: 5px 10px;
            text-align: right;
            color: #333;
            font-size: 16px;
            line-height: 36px;
        }
        .right {
            float: left;
            width: 76%;
            padding: 5px;
            text-align: justify;
            color: #2b2b2b;
            font-size: 14px;

            input, textarea {
                width: 100%;
                padding: 5px 10px;
                color: #2b2b2b;
                font-size: 14px;
                line-height: 24px;
                border-radius: 3px;
                border: 1px solid #ddd;
                transition: border 200ms ease;

                &:focus, &:hover {
                    border: 1px solid #F45757;
                }
            }

            textarea {
                height: 200px;
                resize: none;
            }
            
            .inputTips {
                float: right;
                color: #999;
                font-size: 14px;
                line-height: 20px;
            }
        }

        .replyBtn {
            width: 100px;
            margin: 0 auto;

            button {
                width: 100px;
                height: 32px;
                margin-top: 5px;
                color: #aaa;
                font-size: 14px;
                line-height: 32px;
                background: #F6F6F6;
                border-radius: 3px;
                transition: all 200ms ease;
            }

            .able {
                color: #FFF;
                background: #F45757;
                cursor: pointer;

                &:active {
                    background: #d44343;
                }
            }

            span {
                float: right;
                color: #888;
                font-size: 14px;
                line-height: 42px;
                cursor: pointer;
                user-select: none;

                &:hover {
                    color: #F45757;
                }

                 &:active {
                    color: #d44343;
                }
            }
        }
    }
}
</style>


